<template>
	<view class="page">
		<image class="imagebg" src="./image/imagebakshenqing.png" mode="aspectFill"></image>
		<view class="messageview">
			<view class="messageview-title">注册信息</view>
			<input v-model="username" placeholder="请输入您的姓名" class="input" placeholder-class="placeholder" />

			<input v-model="username" type="number" placeholder="请输入您的手机号码" class="input"
				placeholder-class="placeholder" />

			<view class="codeinputview">
				<input v-model="code" type="number" placeholder="短信验证码" class="inputcode"
					placeholder-class="placeholder" />
				<view @click="getcode" class="getcode">{{codetimes}}</view>
			</view>
			<input v-model="password" type="number" :password="true" placeholder="请设置登录密码" class="input"
				placeholder-class="placeholder" />

			<input v-model="password2" type="number" :password="true" placeholder="请确认登录密码" class="input"
				placeholder-class="placeholder" />

			<input v-model="IDcard" placeholder="请输入您的身份证号码" class="input" placeholder-class="placeholder" />
			<image @click="upload" class="upload" src="./image/cfz.png"></image>
			<view @click="submit" class="submit">提交</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				timer:null,
				codetimes: '获取验证码',
				username: '',
				mobile: '',
				code: '',
				password: '',
				password2: '',
				IDcard: '',
			}
		},
		onUnload() {
			clearInterval(this.timer)
			this.timer = null
		},
		methods: {
			upload() {
				let that = this;
				uni.chooseImage({
					count: 1,
					sizeType: ['original', 'compressed'],
					sourceType: ['album', 'camera'],
					success: function(res) {
						let tempFilePaths = res.tempFilePaths;

					},
				});
			},
			submit() {

			},
			getcode() {

				var that = this
				let sign = this.sort2url({
					'account': this.mobile,
					'type': 5
				});
				var dic = {}
				dic.account = this.mobile
				dic.type = 5
				dic.sign = sign

				uni.request({
					url: '',
					method: 'GET',
					data: dic,
					success: res => {
						uni.showToast({
							title: res.data.data.msg,
							icon:'none'
						});
						if (res.data.data.code == 1002) {
							that.codeFocus = true
							var time = 60
							clearInterval(that.timer)
							that.timer = null
							that.codetimes = '(' + '60s)'
							that.timer = setInterval(function() {
								time = time - 1
								that.codetimes = '(' + time + 's)'
								if (time == 0) {
									clearInterval(that.timer)
									that.timer = null
									that.codetimes = '获取验证码'
								}
							}, 1000);
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			sort2url(arr1) {
				var newkey = Object.keys(arr1).sort();
				var newObj = {};
				for (var i = 0; i < newkey.length; i++) {
					newObj[newkey[i]] = arr1[newkey[i]];
				}
				var text = "";
				for (var index in newObj) {
					text = text + index + "=" + newObj[index] + "&";
				}
				text = text.substr(0, text.length - 1);
				text += '&' + '400d069a791d51ada8af3e6c2979bcd7';
				return md5_js(text);
			},
		}
	}
</script>

<style>
	.upload {
		margin-top: 15px;
		width: calc(100% - 30px);
		margin-left: 15px;
		height: 140px;
	}

	.submit {
		margin-top: 15px;
		border-radius: 4px;
		width: calc(100% - 30px);
		margin-left: 15px;
		height: 42px;
		line-height: 42px;
		text-align: center;
		color: #FFFFFF;
		background: linear-gradient(to bottom, rgba(255, 205, 122, 1), rgba(254, 128, 4, 1));

	}

	.getcode {
		position: absolute;
		right: 0;
		background-color: rgba(255, 239, 223, 1);
		width: 140px;
		height: 44px;
		line-height: 44px;
		border-radius: 4px;
		color: rgba(255, 77, 41, 1);
		text-align: center;

	}

	.codeinputview {
		position: relative;
		width: calc(100% - 30px);
		margin-left: 15px;
		margin-top: 15px;
		display: flex;
		flex-direction: row;
		height: 42px;
	}

	.inputcode {
		font-size: 16px;
		width: calc(100% - 170px);
		height: 42px;
		border: 1px solid rgba(217, 217, 217, 1);
		border-radius: 4px;
		padding-left: 10px;
		padding-right: 10px;
	}

	.placeholder {
		font-size: 16px;
		color: rgba(152, 152, 152, 1);
	}

	.input {
		font-size: 16px;
		margin-top: 15px;
		width: calc(100% - 50px);
		margin-left: 15px;
		height: 42px;
		border: 1px solid rgba(217, 217, 217, 1);
		border-radius: 4px;
		padding-left: 10px;
		padding-right: 10px;
	}

	.messageview-title {
		margin-left: 15%;
		margin-top: -21px;
		width: calc(70%);
		height: 42px;
		line-height: 42px;
		text-align: center;
		color: rgba(255, 255, 255, 1);
		font-size: 18px;
		font-weight: bold;
		border-radius: 24px;
		background: linear-gradient(to bottom, rgba(255, 205, 122, 1), rgba(254, 128, 4, 1));
	}

	.messageview {
		position: absolute;
		z-index: 999;
		width: calc(100% - 20px);
		margin-left: 10px;
		background-color: #FFFFFF;
		border-radius: 15px 15px 0 0;
		padding-bottom: 20px;
		margin-top: 220px;
	}

	.page {
		width: 100%;
		height: 100%;
		background-color: rgba(255, 145, 5, 1);
		overflow: hidden;
	}

	page {
		width: 100%;
		height: 100%;
		background-color: rgba(255, 145, 5, 1);
		overflow: hidden;
	}

	.imagebg {
		z-index: 998;
		position: absolute;
		width: 100%;
		height: 300px;
	}
</style>
